import React, { useState, useEffect } from "react";
import Page from "../../components/Page";
import axios from "axios";
import { useLocation } from "react-router-dom";
import { Box, Typography, Button } from "@mui/material";
import { ArrowBack, ArrowForward } from "@mui/icons-material";
import NavbarHeader from "../../components/navbar";

const ProductViewPage = () => {
  const location = useLocation();
  const title = location.state?.title;
  const [currProduct, setCurrProduct] = useState({});
  const [currentSlide, setCurrentSlide] = useState(0);

  const handlePrev = () => {
    setCurrentSlide((prevSlide) => (prevSlide - 1 + 15) % 15);
  };

  const handleNext = () => {
    setCurrentSlide((prevSlide) => (prevSlide + 1 + 15) % 15);
  };

  const handleSlide = (index) => {
    setCurrentSlide(index);
  };

  const handleFetchProduct = (data) => {
    const url = "/api/v1/products/";

    axios({
      data: data,
      method: "POST",
      url,
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((res) => {
        const { status, data } = res;
        setCurrProduct(data?.data);
        console.log("data", data);
      })
      .catch((error) => {
        console.log("Error", error);
      });
  };

  const handleEditProduct = (data) => {
    const url = "/api/v1/edit_product/";

    axios({
      data: data,
      method: "POST",
      url,
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((res) => {
        const { status, data } = res;
        setCurrProduct(data?.data);
        console.log("data", data);
      })
      .catch((error) => {
        console.log("Error", error);
      });
  };

  const handleFavouriteClick = () => {
    const data = {
      title: currProduct.title,
      is_favourite: !currProduct.is_favourite,
    };
    handleEditProduct(data);
  };

  const handleAddToCartClick = () => {
    const data = {
      title: currProduct.title,
      is_item_in_cart: !currProduct.is_item_in_cart,
      quantity: currProduct.quantity + 1,
    };
    handleEditProduct(data);
  };

  useEffect(() => {
    handleFetchProduct({ title: title });
  }, []);

  return (
    <Page title="products">
      <NavbarHeader />
      <Box sx={{ marginTop: "10px" }}>
        <Typography variant="h3" align="center">
          {currProduct.title}
        </Typography>

        <Box
          sx={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
          }}
        >
          <Box
            component="div"
            sx={{
              width: "40vw",
              marginLeft: "10vw",
              marginTop: "1rem",
              marginRight: "20px",
            }}
          >
            <Box
              id="carouselExampleControls"
              component="div"
              className="carousel slide"
              data-ride="carousel"
              sx={{
                position: "relative",
              }}
            >
              <Box component="div" className="carousel-inner">
                <Box component="div" className="carousel-item active">
                  <img
                    className="d-block w-100"
                    src={`https://${currProduct[`image_${currentSlide}`]}`}
                    alt={`Slide ${currentSlide + 1}`}
                  />
                </Box>
              </Box>
              <Box
                component="a"
                href="#carouselExampleControls"
                role="button"
                onClick={handlePrev}
                sx={{
                  position: "absolute",
                  top: "50%",
                  left: 0,
                  transform: "translateY(-50%)",
                  backgroundColor: "black",
                  color: "white",
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  minWidth: "48px",
                  minHeight: "48px",
                  p: 1,
                  cursor: "pointer",
                }}
              >
                <ArrowBack fontSize="large" />
              </Box>
              <Box
                component="a"
                href="#carouselExampleControls"
                role="button"
                onClick={handleNext}
                sx={{
                  position: "absolute",
                  top: "50%",
                  right: 0,
                  transform: "translateY(-50%)",
                  backgroundColor: "black",
                  color: "white",
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  minWidth: "48px",
                  minHeight: "48px",
                  p: 1,
                  cursor: "pointer",
                }}
              >
                <ArrowForward fontSize="large" />
              </Box>
            </Box>
            <Box
              sx={{
                marginTop: "1rem",
                display: "flex",
                width: "100%",
                overflowX: "scroll",
                scrollbarWidth: "none",
                msOverflowStyle: "none",
                scrollbarColor: "transparent transparent",
              }}
            >
              {Array.from(Array(15).keys()).map((index) => (
                <Box
                  key={index}
                  component="img"
                  className={`reference-image ${
                    index === currentSlide ? "active" : ""
                  }`}
                  src={`https://${currProduct[`image_${index}`]}`}
                  alt={`Reference ${index + 1}`}
                  onClick={() => handleSlide(index)}
                  sx={{
                    width: "100px",
                    marginRight: "10px",
                    cursor: "pointer",
                  }}
                />
              ))}
            </Box>
          </Box>
          <Box
            className="right_block"
            style={{ marginTop: "50px" }}
            sx={{
              width: "40vw",
              marginRight: "10vw",
              marginTop: "1rem",
            }}
          >
            {currProduct && currProduct["price"] && (
              <Typography variant="span" sx={{ fontWeight: "bold" }}>
                Price:{" "}
                {currProduct["price"].toLocaleString("en-IN", {
                  style: "currency",
                  currency: "INR",
                })}
              </Typography>
            )}
            <Box sx={{ justifyContent: "center", marginTop: "1rem" }}>
              <Button
                variant={currProduct.is_favourite ? "contained" : "outlined"}
                onClick={handleFavouriteClick}
                style={{ marginRight: "1rem" }}
              >
                ADD TO FAV
              </Button>
              <Button
                variant={currProduct.is_item_in_cart ? "contained" : "outlined"}
                onClick={handleAddToCartClick}
                style={{ marginRight: "1rem" }}
              >
                ADD TO CART
              </Button>
            </Box>
            {currProduct && currProduct["description"] && (
              <Box sx={{ marginTop: "1rem" }}>
                {currProduct["description"].split("\n").map((line, index) => (
                  <Typography key={index} variant="p">
                    {line}
                  </Typography>
                ))}
              </Box>
            )}
          </Box>
        </Box>
      </Box>
    </Page>
  );
};

export default ProductViewPage;
